import React, {useEffect} from 'react'
import classes from './Home.module.css'
import {Link} from 'react-router-dom'
import {useDispatch} from 'react-redux'
import {onEditorPageAction} from '../../redux/actions'
import {urls} from '../../router/routes'

const Home = () => {
  const dispatch = useDispatch()

  useEffect(() => {
    if (localStorage.getItem('isEditorPage')) {
      localStorage.setItem('isEditorPage', 'false')
    }
  }, [dispatch])

  const onEditorPage = () => {
    dispatch(onEditorPageAction())
    localStorage.setItem('isEditorPage', 'true')
  }

  return (
    <div className="container">
      <div className={classes.welcome}>
        <h1 className={classes.welcome__title}> ...   Markdown</h1>
        <p className={classes.welcome__text}>      Markdown-</p>
        <Link to={urls.editor}
              onClick={onEditorPage}
              className="link"> </Link>
      </div>
    </div>
  )
}

export default Home
